    #373333 0%,
    #bcfcd9 45%,
    #87fed8 60%,
    #5dfffc 85%,
    #91c3ff 95%
  );
  border-image-slice: 1;
  height: 100%;
}

.item {
  padding-top: 0.5em;
}
.item:first-of-type {
  padding-top: 0;
}

.item:last-of-type {
  padding-bottom: 0.5em;
}

@media screen and (max-width: 1024px) {
  .projects-container {
    display: inline-block;
  }

  .sidenav-sub {
    -webkit-align-items: left;
            align-items: left;
    display: -webkit-inline-flex;
    display: inline-flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-flex-wrap: wrap;
